import React, { useEffect, useState } from 'react'
import './App.scss'
import { create, get } from '@github/webauthn-json/browser-ponyfill'
import { decode } from 'cborg'
import { Buffer } from 'buffer'
import base64url from 'base64url'
import { BaseAuth } from './Auth/BaseAuth/BaseAuth'
import { passkeyType } from './Common/type'
import { PasskeyAuth } from './Auth/PasskeyAuth/PasskeyAuth'
import { MainPage } from './Main/MainPage'
import { TEST_USER } from './Common/const'

// Парсим AuthData
const parseAuthData = (buffer: Buffer) => {
  if (buffer.byteLength < 37) throw new Error('Authenticator Data must be at least 37 bytes long!')

  const rpIdHash = buffer.slice(0, 32)
  // оставляем остаток без 0 - 32
  buffer = buffer.slice(32)

  /* Flags */
  const flagsBuffer = buffer.slice(0, 1)
  buffer = buffer.slice(1)

  const flagsInt = flagsBuffer[0]
  const up = !!(flagsInt & 0x01) // Test of User Presence
  const uv = !!(flagsInt & 0x04) // User Verification
  const at = !!(flagsInt & 0x40) // Attestation data
  const ed = !!(flagsInt & 0x80) // Extension data
  const flags = { up, uv, at, ed, flagsInt }

  const counterBuffer = buffer.slice(0, 4)
  buffer = buffer.slice(4)
  const counter = counterBuffer.readUInt32BE(0)

  /* Attested credential data */
  let aaguid = undefined
  let aaguidBuffer = undefined
  let credIdBuffer = undefined
  let cosePublicKeyBuffer = undefined
  const attestationMinLen = 16 + 2 + 16 + 42 // aaguid + credIdLen + credId + pk

  if (at) {
    // Attested Data
    if (buffer.byteLength < attestationMinLen)
      throw new Error(
        `It seems as the Attestation Data flag is set, but the remaining data is smaller than ${attestationMinLen} bytes. You might have set AT flag for the assertion response.`,
      )

    aaguid = buffer.slice(0, 16).toString('hex')
    buffer = buffer.slice(16)

    aaguidBuffer = `${aaguid.slice(0, 8)}-${aaguid.slice(8, 12)}-${aaguid.slice(
      12,
      16,
    )}-${aaguid.slice(16, 20)}-${aaguid.slice(20)}`

    const credIdLenBuffer = buffer.slice(0, 2)
    buffer = buffer.slice(2)

    const credIdLen = credIdLenBuffer.readUInt16BE(0)
    credIdBuffer = buffer.slice(0, credIdLen)
    buffer = buffer.slice(credIdLen)

    const pubKeyLength = buffer.byteLength
    cosePublicKeyBuffer = buffer.slice(0, pubKeyLength)
    buffer = buffer.slice(pubKeyLength)
  }

  /*  let coseExtensionsDataBuffer = undefined;
  if(ed) { // Extension Data
    let extensionsDataLength = (buffer).byteLength;

    coseExtensionsDataBuffer = buffer.slice(0, extensionsDataLength);
    buffer = buffer.slice(extensionsDataLength);
  }*/

  if (buffer.byteLength) throw new Error('Failed to decode authData! Leftover bytes been detected!')
  console.log('rpIdHash ' + rpIdHash)
  console.log('counter ' + counter)
  console.log('flags ' + Object.entries(flags))
  console.log('counterBuffer ' + counterBuffer)
  console.log('aaguid ' + aaguid)
  console.log(' aaguidBuffer ' + aaguidBuffer)
  console.log('credIdBuffer ' + credIdBuffer)

  return { rpIdHash, counter, flags, counterBuffer, aaguid, credIdBuffer }
}

function App() {
  // eslint-disable-next-line @typescript-eslint/no-var-requires
  const sha256 = require('js-sha256')
  // Проверяем, поддерживается ли авторизация по passkey
  const [isAuthPasskey, setIsAuthPassKey] = useState(false)
  const [currentUser, setCurrentUser] = useState<string | null>(null)
  const [passkeysArr, setPasskeysArr] = useState<passkeyType[]>([])
  const [result, setResult] = useState<string | undefined>()
  const [resultAuthn, setResultAuthn] = useState<string | undefined>()

  // кодируем данные пользователя в base64
  function b64EncodeUnicode(str: string) {
    // first we use encodeURIComponent to get percent-encoded UTF-8,
    // then we convert the percent encodings into raw bytes which
    // can be fed into btoa.
    return btoa(
      encodeURIComponent(str).replace(/%([0-9A-F]{2})/g, function toSolidBytes(match, p1) {
        return String.fromCharCode(Number('0x' + p1))
      }),
    )
  }

  const createNewPasskey = (userName: string) => {
    // в будущем userIdBase64 нужно заменить на индетификатор пользователя с бэкенда
    const userIdBase64 = b64EncodeUnicode(userName)
    const createOptions: CredentialCreationOptions = {
      // Объект, описывающий параметры создания учетных данных WebAuthn
      publicKey: {
        // Объект, описывающий проверяющую сторону, запросившую создание учетных данных
        rp: {
          name: 'Passkeys test page',
          id: 'alexanderpm.github.io',
        },
        // Объект, описывающий учетную запись пользователя, для которого создаются учетные данные
        user: {
          // `userIdBase64` is the user's passkey ID, from the database,
          // base64-encoded.
          id: Uint8Array.from(atob(userIdBase64), (c) => c.charCodeAt(0)),
          // `username` is the user's username. Whatever they would type
          // when signing in with a password.
          name: userName,
          // `displayName` can be a more human name for the user, or
          // just leave it blank.
          displayName: userName,
        },

        // This lists the ids of the user's existing credentials. I.e.
        //   SELECT id FROM passkeys WHERE username = ?
        // and supply the resulting list of values, base64-encoded, as
        // existingCredentialIdsBase64 here.
        excludeCredentials: passkeysArr.map((item) => {
          return {
            type: 'public-key',
            id: Uint8Array.from(atob(item.publicKey), (c) => c.charCodeAt(0)),
            transports: ['internal'],
          }
        }),

        // Boilerplate that advertises support for P-256 ECDSA and RSA
        // PKCS#1v1.5. Supporting these key types results in universal
        // coverage so far.
        pubKeyCredParams: [
          {
            type: 'public-key',
            alg: -7,
          },
          {
            type: 'public-key',
            alg: -257,
          },
        ],

        // Unused during registrations, except in some enterprise
        // deployments. But don't do this during sign-in!
        challenge: new Uint8Array([0]),

        authenticatorSelection: {
          authenticatorAttachment: 'platform',
          requireResidentKey: true,
          residentKey: 'required',
          userVerification: 'required',
        },

        // Сколько вызывающая сторона готова ждать завершение операции создания в миллисекундах
        timeout: 18000,
      },
    }

    create(createOptions).then(
      (pubKeyCredential) => {
        const response = pubKeyCredential
        if (response?.id) {
          setCurrentUser(userName)
          setPasskeysArr((oldArray) => [
            ...oldArray,
            { username: userName, publicKey: response.id },
          ])
          setResult(JSON.stringify(response.toJSON()))
          const hashed = sha256(response.toJSON().response.clientDataJSON)
          alert('выводим хэш + /// ' + hashed)
        }
      },
      (response) => handleCreationError(response),
    )
  }

  function handleCreationError(e: Error) {
    if (e instanceof DOMException) {
      switch (e.name) {
        case 'InvalidStateError':
          console.log('InvalidStateError')
          return

        case 'NotAllowedError':
          console.log('NotAllowedError')
          return
      }
    }

    console.log(e)
  }

  useEffect(() => {
    // Проверяем есть ли на устройстве локальный аутентификатор и будет ли браузер поддерживать пароли
    if (
      !window.PublicKeyCredential ||
      !(PublicKeyCredential as any).isConditionalMediationAvailable
    ) {
      setIsAuthPassKey(false)
      return
    }
    Promise.all([
      (PublicKeyCredential as any).isConditionalMediationAvailable(),
      // Статический метод, возвращающий Promise.
      // Разрешается в true если есть аутентификатор, привязанный к платформе, способен проверить пользователя.
      PublicKeyCredential.isUserVerifyingPlatformAuthenticatorAvailable(),
    ]).then((values) => {
      if (values.every((x) => x === true)) {
        setIsAuthPassKey(true)
      }
    })
  }, [])

  // Вот эта проверка должна быть на сервере
  const request = {
    type: 'public-key',
    id: 'OG16p-kdgAS-s0RAWPlRBQ',
    rawId: 'OG16p-kdgAS-s0RAWPlRBQ',
    authenticatorAttachment: 'platform',
    response: {
      clientDataJSON:
        'eyJ0eXBlIjoid2ViYXV0aG4uY3JlYXRlIiwiY2hhbGxlbmdlIjoiQUEiLCJvcmlnaW4iOiJodHRwczpcL1wvYWxleGFuZGVycG0uZ2l0aHViLmlvIiwiYW5kcm9pZFBhY2thZ2VOYW1lIjoiY29tLmNocm9tZS5jYW5hcnkifQ',
      attestationObject:
        'o2NmbXRkbm9uZWdhdHRTdG10oGhhdXRoRGF0YViUbtzKb-f8pS3AlWlAns_Bpa2qzBaHsoUYjLQfHou81ohdAAAAAAAAAAAAAAAAAAAAAAAAAAAAEDhteqfpHYAEvrNEQFj5UQWlAQIDJiABIVggrPnbiSvy8_7fjcpiwgm7xhR6P3xuXWntByXeON7WdYYiWCD6hoUZ5aHr22izBKs4sl9tPi1s3UV6JEMSM4XS_UQNAw',
      transports: ['hybrid', 'internal'],
    },
    clientExtensionResults: {},
  }

  const clientDataJSON = request.response.clientDataJSON
  // декодируем полученные данные из clientDataJSON
  type clientDataType = {
    androidPackageName?: string
    challenge?: string
    origin?: string
    type?: string
  }
  const clientData = JSON.parse(
    new Buffer(clientDataJSON, 'base64').toString('ascii'),
  ) as clientDataType

  // Проверяем вызов challenge, в будущем будет уникальная последовательность, сейчас пока закодированное в формате Base64 имя пользователя
  // Смысл в том, что после каждой авторизации обновляем это последовательность и потом проверяем на актуальность при следующей авторизации
  if (
    clientData.challenge &&
    new Buffer(clientData.challenge, 'base64').toString('ascii') === TEST_USER.login
  ) {
    console.log('Challenge. Проверка пройдена.')
  }
  if (clientData.origin === 'https://alexanderpm.github.io') {
    console.log('Origin. Проверка пройдена.')
  }
  if (clientData.type === 'webauthn.create') {
    console.log('Type. Регистрация нового пользователя.')

    const attestationObject = request.response.attestationObject
    const attestationObjectBuffer = Buffer.from(attestationObject, 'base64')
    const decodedBuffer = decode(attestationObjectBuffer)
    console.log(decodedBuffer)
    parseAuthData(decodedBuffer.authData)
  }
  if (clientData.type === 'webauthn.get') {
    console.log('Type. Авторизация.')
  }

  const isPublicKeyForCurrentUser = (userName: string | null) => {
    if (passkeysArr.length < 1 || !userName) {
      return false
    }
    let result = false
    passkeysArr.forEach((passkeys) => {
      if (passkeys.username === userName) {
        result = true
      }
    })
    return result
  }

  const handlerOnClick = () => {
    if (currentUser) {
      const publicKey = isPublicKeyForCurrentUser(currentUser)
      if (!publicKey) {
        createNewPasskey(currentUser)
      }
      if (publicKey) {
        // To abort a WebAuthn call, instantiate an `AbortController`.
        const abortController = new AbortController()

        const publicKeyCredentialRequestOptions: CredentialRequestOptions = {
          mediation: 'conditional' as CredentialMediationRequirement,
          publicKey: {
            // Server generated challenge
            challenge: Uint8Array.from(atob(b64EncodeUnicode(currentUser)), (c) => c.charCodeAt(0)),
            // The same RP ID as used during registration
            rpId: 'alexanderpm.github.io',
          },
          signal: abortController.signal,
        }
        get(publicKeyCredentialRequestOptions).then(
          (response) => {
            setResultAuthn(JSON.stringify(response))
            if (response?.id) {
              const hashed = sha256(response.toJSON().response.clientDataJSON)
              alert('выводим хэш + /// ' + hashed)
            }
          },
          () => alert('error'),
        )
      }
    }
  }

  return (
    <div className='App'>
      <h2>Testing password and passkey authorization</h2>
      {/* Авторизация по логину и паролю */}
      {!currentUser && <BaseAuth setCurrentUser={setCurrentUser} />}
      {!currentUser && isAuthPasskey && <PasskeyAuth />}
      {/* Успешная авторизация */}
      {currentUser && (
        <MainPage
          isAuthPasskey={isAuthPasskey}
          currentUser={currentUser}
          setCurrentUser={setCurrentUser}
          isPublicKeyForCurrentUser={isPublicKeyForCurrentUser}
          handlerOnClick={handlerOnClick}
        />
      )}
      {result && <p style={{ wordBreak: 'break-word' }}>{result}</p>}
      {resultAuthn && <p style={{ wordBreak: 'break-word', color: 'red' }}>{resultAuthn}</p>}
    </div>
  )
}

export default App
